import { graphql, useStaticQuery } from "gatsby";

export interface SiteMetadata {
  title: string;
  description: string;
  author: {
    name: string;
    email: string;
    content: string;
  };
  social: {
    github: string;
  };
  technologies: { name: string; url: string }[];
}

export default (): SiteMetadata => {
  const data = useStaticQuery(graphql`
    {
      site {
        siteMetadata {
          title
          description
          author {
            name
            email
            content
          }
          social {
            github
          }
          technologies {
            name
            url
          }
        }
      }
    }
  `);

  return data.site.siteMetadata;
};
