import {
  AppBar,
  Button,
  Theme,
  Toolbar,
  Typography,
  useMediaQuery,
} from "@material-ui/core";
import Brightness3Icon from "@material-ui/icons/Brightness3";
import Brightness7Icon from "@material-ui/icons/Brightness7";
import EmailIcon from "@material-ui/icons/Email";
import GitHubIcon from "@material-ui/icons/GitHub";
import WorkIcon from "@material-ui/icons/Work";
import { makeStyles } from "@material-ui/styles";
import React, { useCallback, useContext } from "react";
import scrollTo from "gatsby-plugin-smoothscroll";
import useSiteMetadata from "../hooks/useSiteMetadata";
import themes, { ThemeContext } from "../theme";

type ButtonVariant = "text" | "contained" | "outlined";

export default function Header() {
  const themeContext = useContext(ThemeContext);
  const classes = useStyles();
  const windowAboveSmall = useMediaQuery(
    themes[themeContext[0].mode].breakpoints.up("md"),
  );
  const { author, social } = useSiteMetadata();
  const links = [
    { label: "Github", url: social.github, icon: GitHubIcon },
    {
      label: "Email",
      url: `mailto:${author.email}`,
      icon: EmailIcon,
    },
  ];

  const getButtonVariant = useCallback(() => {
    if (!windowAboveSmall) return "text";
    return themeContext[0].mode === "light" ? "contained" : "outlined";
  }, [themeContext[0].mode, windowAboveSmall]);

  const createButton = useCallback(
    (props: {
      label: string;
      icon: JSX.Element;
      url?: string;
      onClick?: onClickEvent;
    }) => {
      const { label, icon, url, onClick } = props;
      const attributes = {
        key: label,
        "aria-label": label,
        className: classes.linkButton,
        variant: getButtonVariant() as ButtonVariant,
        color:
          themeContext[0].mode === "light"
            ? ("primary" as "primary")
            : undefined,
        href: url || "",
        onClick: onClick,
      };
      return windowAboveSmall ? (
        <Button {...attributes} size="large" endIcon={icon}>
          {label}
        </Button>
      ) : (
        <Button {...attributes}>{icon}</Button>
      );
    },
    [themeContext[0].mode, windowAboveSmall],
  );

  return (
    <AppBar component="header" position="static">
      <Toolbar className={classes.wrapper}>
        <div className={classes.title}>
          <Typography variant="h4">Joshua Sosa</Typography>
          <Typography variant="h6" color="textSecondary">
            Software Engineer
          </Typography>
        </div>
        <div className={classes.links}>
          {createButton({
            label: "Projects",
            icon: <WorkIcon />,
            onClick: () => scrollTo("#projects"),
          })}
          {links.map((link) => {
            return createButton({
              label: link.label,
              icon: <link.icon />,
              url: link.url,
            });
          })}
          {createButton({
            label:
              themeContext[0].mode === "light" ? "Dark Mode" : "Light Mode",
            icon:
              themeContext[0].mode === "light" ? (
                <Brightness3Icon />
              ) : (
                <Brightness7Icon />
              ),
            onClick: themeContext[1],
          })}
        </div>
      </Toolbar>
    </AppBar>
  );
}

const useStyles = makeStyles((theme: Theme) => {
  const { breakpoints, palette, spacing } = theme;
  return {
    wrapper: {
      backgroundColor: palette.background.paper,
      width: "100%",
      [breakpoints.down("xs")]: {
        display: "grid",
        gridTemplateRows: "repeat(3, auto)",
      },
    },
    title: {
      padding: spacing(2, 0),
      flexGrow: 1,
      whiteSpace: "nowrap",
      color:
        palette.type === "light" ? palette.text.primary : palette.common.white,
      [breakpoints.down("xs")]: {
        textAlign: "center",
      },
    },
    links: {
      display: "grid",
      gridTemplateColumns: "repeat(4, auto)",
      columnGap: "1em",
    },
    linkButton: {
      color:
        palette.type === "dark" ? palette.common.white : palette.primary.main,
      [breakpoints.up("md")]: {
        color: palette.common.white,
      },
    },
  };
});
