import {
  Container,
  CssBaseline,
  Theme,
  useMediaQuery,
} from "@material-ui/core";
import { makeStyles, ThemeProvider } from "@material-ui/styles";
import React, { useCallback, useState } from "react";

import useSiteMetadata from "../hooks/useSiteMetadata";
import Themes, { ThemeContext, ThemeMode } from "../theme";
import Footer from "./footer";
import Header from "./header";

export default function Layout(props: {
  children: JSX.Element | JSX.Element[];
}) {
  const classes = useStyles();
  const { children } = props;
  const { title } = useSiteMetadata();
  const prefersDarkMode = useMediaQuery("(prefers-color-scheme: dark)");
  const mode =
    typeof window !== "undefined"
      ? (localStorage.getItem("themeMode") as ThemeMode)
      : prefersDarkMode
      ? "dark"
      : "light";
  const [localTheme, setLocalTheme] = useState({ mode });
  const changeTheme = useCallback(() => {
    const mode = localTheme.mode === "light" ? "dark" : "light";
    localStorage.setItem("themeMode", mode);
    setLocalTheme({ mode: mode });
  }, [localTheme]);

  return (
    // <ThemeContext.Provider value={[localTheme, changeTheme]}>
    //   <ThemeProvider theme={Themes[localTheme.mode]}>
    <div className={classes.root}>
      <Header />
      {children}
      <Footer />
    </div>
    //   </ThemeProvider>
    // </ThemeContext.Provider>
  );
}

const useStyles = makeStyles(() => ({
  root: {
    display: "flex",
    flexDirection: "column",
    minHeight: "100vh",
  },
}));
