import { Container, Link, Theme, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import React, { FC } from "react";

import useSiteMetadata from "../hooks/useSiteMetadata";

const Footer: FC = () => {
  const classes = useStyles();
  const { author, technologies } = useSiteMetadata();
  return (
    <footer className={classes.footer}>
      <Container>
        <Typography variant="body1" color="textSecondary">
          {`Copyright  ${new Date().getFullYear()} All Rights
          Reserved`}
        </Typography>
        <Typography variant="body1" color="textSecondary">
          Built with
          {technologies.map((tech, idx) => {
            return (
              <span key={tech.name}>
                {idx === technologies.length - 1 ? " and " : " "}
                <Link href={tech.url} color="inherit">
                  {tech.name}
                </Link>
                {idx !== technologies.length - 1 ? "," : ""}
              </span>
            );
          })}
          &nbsp;by&nbsp;{author.name}
        </Typography>
      </Container>
    </footer>
  );
};

export default Footer;

const useStyles = makeStyles((theme: Theme) => ({
  footer: {
    padding: theme.spacing(3, 2),
    textAlign: "center",
    marginTop: "auto",
    backgroundColor: theme.palette.background.paper,
  },
}));
